import React, { useEffect } from 'react';
import { Button } from '../Button';

export const Row = ({ item, onItemChange }) => {
  return (
    <tr>
      {Object.keys(item).map(key => {
        if (key === 'id') {
          return (
            <th key={key} scope="row">
              {item[key].value}
            </th>
          );
        }
        if (key === 'editable') {
          return (
            <td key={key}>
              <Button
                color={!item[key].value ? 'primary' : 'danger'}
                onClick={event => onItemChange({ ...item, [key]: { value: !item[key].value } })}
              >
                {!item[key].value ? 'edit' : 'done'}
              </Button>
            </td>
          );
        } else {
          return (
            <td key={key}>
              {!!item.editable.value ? (
                <input
                  className="form-control"
                  type={item[key].type}
                  value={item[key].value}
                  onChange={event => {
                    console.log(item);
                    onItemChange({
                      ...item,
                      [key]: { value: event.target.value, type: item[key].type },
                    });
                    console.log(item);
                  }}
                />
              ) : (
                item[key].value
              )}
            </td>
          );
        }
      })}
    </tr>
  );
};
